
// our partners
.homepageOurPartners{
    background-color: var(--brighter);
    overflow: hidden;

    .partnersImageLine{
        display: flex;
        transition: transform 5s ease;
        animation-name: PartnersLine;
        animation-duration: 10000s;
        direction: ltr;
    }

	.partnersImageLine.one.mobile{
		display: none;
	}

    .ourPartnersTitle{
        font-family: var(--fontSecondary);
		font-size: clamp(30px, 4vw, 40px);
        font-weight: 700;
        margin-bottom: 25px;
        text-align: center;
    }
    .homepageTopImageItem{
        display: flex;

        img{
            height: 100px;
            width: 200px;
            margin: 0 10px;
						object-fit: contain;
            // margin: 40px 0;
            // padding: 0 20px;
        }
    }
}

@mixin white-gradient {
	background: linear-gradient(to right,  rgba(255,255,255,1) 0%,rgba(255,255,255,0) 100%);
}

$animationSpeed: 40s;

// Animation
@keyframes scroll {
	0% { transform: translateX(0); }
	100% { transform: translateX(calc(-400px * 4))}
}
@keyframes scrollReverse {
	100% { transform: translateX(0); }
	0% { transform: translateX(calc(-400px * 4))}
}


// Styling
.slider {
	background: white;
	// box-shadow: 0 10px 20px -5px rgba(0, 0, 0, .125);
	height: 100px;
	margin: 36px auto;
	overflow:hidden;
	position: relative;
	width: 2000px;

	// &::before,
	// &::after {
	// 	@include white-gradient;
	// 	content: "";
	// 	height: 100px;
	// 	position: absolute;
	// 	width: 300px;
	// 	z-index: 2;
	// }

	// &::after {
	// 	right: 0;
	// 	top: 0;
	// 	transform: rotateZ(180deg);
	// }

	// &::before {
	// 	left: 0;
	// 	top: 0;
	// }

	.slide-track {
		animation: scroll $animationSpeed linear infinite;
		display: flex;
		width: calc(400px * 8);
	}

	.slide-track-reverse {
		animation: scrollReverse $animationSpeed linear infinite;
		display: flex;
		width: calc(400px * 8);
	}

	.slide {
		height: 100px;
		width: 400px;
	}
}

// media
@media only screen and (max-width: 830px){
	// .slider::before, .slider::after{
	// 	width: 150px;
	// }
	.slider{
		height: 50px;

		.slide{
			height: 50px;
			width: fit-content;

			.homepageTopImageItem{
				height: 100%;
				width: auto;

				img{
					height: 90%;
					width: auto;
				}
			}
		}
	}
	.homepageOurPartners{

        .ourPartnersTitle{
            margin-bottom: 0 !important;
			margin: auto;
			width: 70%;
        }

		.partnersImageLine.one.mobile{
			display: flex;
		}
	}
}
