.bookingPopupContainer{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #0000002f;
    z-index: 9999;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: background-color 0.2s ease-in-out;

    .visibleArea{
        background-color: var(--bright);
        // height: 704px;
        width: 1140px;
        left: 2255px;
        top: 1495px;
        border-radius: 14px;
        display: flex;
        align-items: center;
        flex-direction: column;
        justify-content: flex-start;
        padding: 20px;

        .popupTitle{
            font-family: var(--fontSecondary);
            font-size: 40px;
            font-weight: 700;
            line-height: 48px;
            letter-spacing: 0em;
            text-align: start;
            position: relative;
            width: 100%;
            text-align: center;

            .xIcon{
                background-color: var(--highlight);
                height: 40px;
                width: 40px;
                right: 0px;
                top: 0px;
                border-radius: 0px;
                position: absolute;
                border-radius: 50%;
                display: flex;
                justify-content: center;
                align-items: center;
                cursor: pointer;

                .homepageTopImageItem{
                    display: flex;
                    justify-items: center;
                    align-items: center;
                }
                
            }
        }

        .popupContent{
            display: flex;
            justify-content: space-between;
            width: 100%;
            padding: 40px 40px 0;

            .popupLeft,
            .popupRight{
                display: flex;
                flex-direction: column;
                text-align: start;
                margin: 22px;
                
                .normalText{
                    font-family: var(--fontPrimary);
                    font-size: 16px;
                    font-weight: 500;
                    line-height: 20px;
                    letter-spacing: 0em;
                    text-align: start;
                }

                // select
                .css-b62m3t-container{
                    border: solid var(--lighterHighlight) 1px;
                    border-radius: 5px;
                    margin-top: 20px;
                    width: 166px;

                    *{
                        transition: none;
                    }

                    *:not(.css-1nmdiq5-menu){
                        border: none !important;
                        box-shadow: none !important;
                        cursor: pointer;
                    }

                    .css-1nmdiq5-menu{
                        box-shadow: 0px 10px 10px 0px #00000024;
                        font-family: var(--fontPrimary);
                        font-size: 16px;
                        font-weight: 500;
                        line-height: 19px;
                        letter-spacing: 0em;

                        *{
                            background-color: var(--brighter);

                            *:hover{
                                color: var(--highlight);
                                cursor: pointer;
                            }
                            
                            .css-tr4s17-option{
                                color: var(--highlight);
                                cursor: pointer;
                            }
                        }

                    }
                    
                    .css-13cymwt-control{
                        border: none;
                    }

                    .css-1jqq78o-placeholder, .css-1dimb5e-singleValue{
                        color: var(--highlight);
                        font-weight: 500;
                        font-family: var(--fontPrimary);
                        font-size: 16px;
                    }

                    .css-1fdsijx-ValueContainer{
                        padding-right: 0;
                    }

                    .css-1u9des2-indicatorSeparator{
                        display: none;
                    }   

                    .css-1hb7zxy-IndicatorsContainer{
                        clip-path: polygon(0 0, 50% 100%, 100% 0);
                        background-color: var(--highlight);
                        width: 10px;
                        height: 7.5px;
                        margin: auto 11px;
                    }

                    .css-1xc3v61-indicatorContainer{
                        display: none;
                    }

                    .css-t3ipsp-control:hover{
                        border: none !important;
                        box-shadow: none !important;
                    }
                }
            }

            

            .popupLeft{
                .homepageTopImageItem{
                    margin-bottom: 30px;
                }


                .popupSubTitle{
                    margin-top: 25px;
                    font-family: var(--fontPrimary);
                    font-size: 23px;
                    font-weight: 500;
                    line-height: 28px;
                    letter-spacing: 0em;
                    text-align: start;
                    width: 272px;
                }

                .timeInMinutes{
                    display: flex;
                    justify-content: space-between;
                    width: 105px;
                    margin-top: 35px;
                    font-family: var(--fontPrimary);
                    font-size: 19px;
                    font-weight: 500;
                    line-height: 23px;
                    letter-spacing: 0em;
                    text-align: start;
                    font-size: 20px;
                    align-items: center;
                    
                    .clockIcon{
                        width: 24px;
                        height: 24px;
                        margin-top: 3px;

                        .homepageTopImageItem{
                            display: flex;
                            justify-content: center;
                            align-items: center;
                            margin: 0;
                        }
                    }
                }

                .scheduleDescription{
                    margin-top: 35px;
                    font-family: var(--fontPrimary);
                    font-size: 16px;
                    font-weight: 400;
                    line-height: 19px;
                    letter-spacing: 0em;
                    text-align: start;
                    width: 235px;
                }

            }

            .popupRight{
                flex-grow: 1;
                flex-direction: row;
                border-radius: 20px;
                overflow: hidden;
                box-shadow: 5px 20px 40px 10px #00000012;


                .rightPopupHighlited{
                    background-color: var(--highlight);
                    color: var(--brighter);
                    padding: 25px;
                    display: flex;
                    flex-direction: column;
                    align-items: center;

                    .customerSeviceImage{

                        .homepageTopImageItem{
                            width: 65px;
                            height: 65px;
                            border-radius: 50%;
                            overflow: hidden;
                            border: solid var(--brighter) 3px;

                            img{
                                width: 100%;
                                height: 100%;
                                object-fit: cover;
                            }
                        }
                    }
                    
                    .zoomCallTitle{
                        font-family: var(--fontPrimary);
                        font-size: 23px;
                        font-weight: 500;
                        line-height: 20px;
                        letter-spacing: 0em;
                        text-align: center;
                        margin: 30px 0;

                    }

                    // time picker
                    .timeTableArea{
                        .react-calendar__navigation__arrow.react-calendar__navigation__prev2-button, .react-calendar__navigation__arrow.react-calendar__navigation__next2-button{
                            display: none;
                        }

                        .react-calendar__navigation__arrow.react-calendar__navigation__next-button{
                            order: 2;
                        }

                        .react-calendar__navigation__arrow.react-calendar__navigation__prev-button{
                            order: 1
                        }

                        .react-calendar__month-view > div{
                            width: min-content;
                        }

                        .react-calendar__month-view__days__day.react-calendar__month-view__days__day--neighboringMonth{
                            color: var(--lightGray)
                        }

                        .react-calendar__tile.react-calendar__tile--now{
                            color: var(--pureRed);
                        }

                        .react-calendar__tile.react-calendar__tile--active{
                            abbr{
                                background-color: var(--brighter);
                                color: var(--highlight);
                                border-radius: 50%;
                            }
                        }

                        .react-calendar__month-view__days__day, .react-calendar__month-view__weekdays__weekday, .react-calendar__navigation__prev-button{
                            background-color: var(--highlight);
                            color: var(--brighter);
                            padding: 0;
                            border: none;
                            width: 40px;
                            max-width: 40px;
                            height: 40px;
                            text-align: center;
                            text-decoration: none;
                            display: flex;
                            justify-content: center;
                            align-items: center;
                            font-size: 14px;
                            font-weight: 400;
                        }

                        .react-calendar__year-view__months, .react-calendar__decade-view__years, .react-calendar__century-view__decades{
                            width: 280px;
                            justify-content: space-between;
                        }

                        .react-calendar__year-view__months__month, .react-calendar__decade-view__years__year{
                            background-color: var(--highlight);
                            color: var(--brighter);
                            padding: 0;
                            border: none;
                            width: 60px;
                            max-width: 60px;
                            height: 40px;
                            text-align: center;
                            text-decoration: none;
                            display: flex;
                            justify-content: center;
                            align-items: center;
                            font-size: 14px;
                            font-weight: 400;

                            *{
                                white-space: nowrap;
                                overflow: hidden;
                                text-overflow: ellipsis;
                                width: 40px;
                            }
                        }

                        .react-calendar__century-view__decades__decade{
                            background-color: var(--highlight);
                            color: var(--brighter);
                            padding: 0;
                            border: none;
                            width: 60px;
                            max-width: 60px;
                            height: 60px;
                            text-align: center;
                            text-decoration: none;
                            display: flex;
                            justify-content: center;
                            align-items: center;
                            font-size: 14px;
                            font-weight: 400;
                        }
                        
                        .react-calendar__month-view__weekdays__weekday > *{
                            text-decoration: none;
                        }

                        .react-calendar__month-view__days {
                            button{
                                abbr{
                                    width: 100%;
                                    height: 100%;
                                    display: flex;
                                    justify-content: center;
                                    align-items: center;
                                }
                            }
                            button:hover{
                                abbr{

                                    background-color: var(--brighter) !important;
                                    color: var(--highlight);
                                    border-radius: 50%;
                                }
                            }
                        }

                        .react-calendar__navigation{
                            display: flex;
                            border-bottom: solid var(--brighter) 1px;
                            margin-bottom: 20px;
                            padding-bottom: 15px;
                        }

                        .react-calendar__navigation__label, .react-calendar__navigation__arrow{
                            background-color: var(--highlight);
                            border: none;
                            color: var(--brighter);
                            font-size: 20px;
                            font-weight: 700;

                        }
                        
                        .react-calendar__navigation__label{
                            text-align: start;
                        }
                    }
                }

                .rightPopupBright{
                    padding: 25px;
                    flex-grow: 1;
                    background-color: var(--brighter);
                    
                    .popupQuestion, .popupQuestionSecond{
                        font-family: var(--fontPrimary);
                        font-size: 16px;
                        font-weight: 500;
                        text-align: start;
                        margin-bottom: 20px;
                    }

                    .popupQuestionSecond{
                        margin-top: 40px;
                    }

                    .minutesNeeded{
                        display: flex;

                        *{
                            height: 40px;
                            width: 130px;
                            padding: 10px;
                            display: flex;
                            justify-content: center;
                            align-items: center;
                            font-family: var(--fontPrimary);
                            font-size: 16px;
                            font-weight: 500;
                            cursor: pointer;
                            background-color: var(--grayBackground);
                            color: var(--grayFont);
                            transition: background-color 0.2s ease-in-out
                        }

                        *:hover{
                            background-color: var(--backgroundHighlight);
                            color: var(--highlight)
                        }

                        .selected{
                            background-color: var(--backgroundHighlight);
                            color: var(--highlight)
                        }

                        .leftMinutes{
                            border-radius: 10px 0 0 10px;
                        }

                        .rightMinutes{
                            border-radius: 0 10px 10px 0;
                        }
                    }

                    .css-b62m3t-container{
                        width: 100%;
                    }

                    .hourPicker{
                        width: 100%;
                        display: flex;
                        flex-direction: column;
                        height: 275px;
                        overflow-y: scroll;
                        margin-top: 24px;
                        padding-right: 10px;

                        .hourPickerItem{
                            height: 40px;
                            border-radius: 0px;
                            padding: 10px;
                            border: solid var(--dark);
                            display: flex;
                            justify-content: center;
                            align-items: center;
                            font-family: var(--fontPrimary);
                            font-size: 16px;
                            font-weight: 500;
                            margin: 8px 0;
                            cursor: pointer;
                            transition: background-color 0.2s ease-in-out;
                        }
                        
                        .hourPickerItem:hover, .hourPickerItem.selected{
                            background-color: var(--dark);
                            color: var(--brighter);
                        }
                    }
                }

            }
            
            .popupRight.hide{
                display: none;
            }

        }
    }
}
// ar
.ar .bookingPopupContainer{
    .popupContent{       

        .popupLeft,
        .popupRight{
            // select
            .css-1fdsijx-ValueContainer{
                padding-left: 0px;
                padding-right: 8px !important;
            }
            .react-calendar__month-view__weekdays__weekday > *{
                // @debug string.slice("Helvetica Neue", 1, 3);
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;
            }
            .react-calendar__month-view__days__day, .react-calendar__month-view__weekdays__weekday, .react-calendar__navigation__prev-button{
                width: 45px !important;
                max-width: 45px !important;
            }
        }

        .react-calendar__navigation__label{
            text-align: right !important;
        }

        .popupRight{
            box-shadow: 5px 20px 40px 10px #00000012;

            .popupQuestion, .popupQuestionSecond{
                text-align: right !important;
            }

            .minutesNeeded{
                flex-direction: row-reverse;
                justify-content: flex-end;
            }

            .hourPicker{
                padding-right: 0px !important;
                padding-left: 10px;
            }
        }

        .popupLeft{
            align-items: flex-start;

            .popupSubTitle{
                text-align: right;
            }

            .scheduleDescription{
                text-align: right;
            }
        }
    }
}