
// image that's gonna turn into video
.imageToVideo{
    margin-right: 25px;
    position: fixed;
    // right: 120px;
    // padding-right: 10%;
    width: 100%;
    height: 100%;
    // right: clamp(1150px, 90%, 1700px);
    // padding-top: 678px;
    z-index: 1;
    margin: auto;
    pointer-events: none;

    .homepageTopImageItem{
        width: clamp(250px, 90%, 1700px);
        margin: auto;
        display: flex;
        justify-content: end;
        // padding-top: 578px;


        img{
            height: clamp(90px, 8vw, 160px);
            width: clamp(155px, 15vw, 300px);

            border-radius: 9px;
            object-fit: cover;
            box-shadow: 0px 21px 40px rgba(0, 0, 0, 0.2);
            transition: all 0.1s ease-in-out;
            margin-top: 578px;
            margin-right: 100px;
            margin-top: 353px;
            margin-right: 75px;
            margin-top: clamp(10px, 60vh, 578px);
            margin-right: clamp(0px, 7vw, 100px);
        }
    }
}

// homepage
.homepageTop{
    padding: 165px 0;
    font-family: var(--fontSecondary);
    font-style: normal;
    font-weight: 700;
    text-align: start;
    display: flex;
    justify-content: space-between;
    margin: auto;
    width: clamp(250px, 90%, 1700px);

    .homepageLeft{

        .mainSiteIntro{
            // font-size: 58px;
            font-size: clamp(35px, 4vw, 58px);
            height: 80px;
            line-height: 80px;
            text-shadow: -1px 0 black, 0 1px black, 1px 0 black, 0 -1px black;
            color: var(--brighter);
            white-space: pre;
        }

        .secondSiteIntro{
            font-size: 52px;
            height: 63px;
            overflow: hidden;
            font-size: clamp(35px, 3.5vw, 52px);

            .moveUp{
                *{
                    line-height: 55px;
                    height: 65px;
                    overflow: hidden;
                    transition: transform 1s ease-in-out;
                }
            }

        }

        .thirdSiteIntro{
            font-family: var(--fontPrimary);
            font-style: normal;
            font-weight: 400;
            font-size: clamp(17px, 2vw, 28px);
            // width: 513px;
            width: clamp(250px, 35vw, 513px);
            margin-top: 40px;
        }

        .getInTouch{
            text-transform: uppercase;
            padding: 22px 38px;
            background: var(--dark);
            box-shadow: 0px 20px 40px rgba(0, 0, 0, 0.13);
            border-radius: 10px;
            color: var(--brighter);
            border: none;
            font-size: 24px;
            font-weight: 600;
            margin-top: 75px;
            transition: all 0.2s ease-in-out;
            cursor: pointer;
        }

        .getInTouch:hover{
            background-color: var(--lightHighlight);
        }
    }
    .homepageRight{
        margin: 0 20px;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        flex-grow: 1;
        z-index: 2;

        .homepageTopImageItem{
            img{
                box-shadow: 0px 21px 40px rgb(0 0 0 / 20%);
                height: clamp(90px, 8vw, 160px);
                width: clamp(155px, 15vw, 300px);
                border-radius: 9px;
            }
        }

        .homepageImagesContainerLeft{
            text-align: right;
            padding-left: 20px;

            .homepageTopImageItem:first-of-type{
                margin-bottom: 100px;
            }

            .homepageTopImageItem:nth-of-type(2){
                // width: 276px;
                text-align: center;

                img{
                    margin-top: 25px;
                    object-fit: cover;
                    // box-shadow: none;
                    // height: 184px;
                    // width: 276px;
                    // border-radius: 9px;
                }
            }
        }

        .homepageImagesContainerRight{
            text-align: right;
            padding-left: 20px;

            .homepageTopImageItem:first-of-type{
                margin-bottom: 220px;
                margin-top: 70px;
            }

            .homepageTopImageItem:nth-of-type(2){
                display: none;
            }
            // .homepageTopImageItem:last-of-type{
            //     margin-right: 25px;
            //     position: fixed;
            //     right: 120px;
            //     top: 678px;
            // }
        }

    }
}
// arabic for top of homepage
.ar{
    .homepageTop{
        .homepageRight{
            .homepageImagesContainerRight{
                text-align: start;
                padding-left: 0;
                padding-right: 20px;
            }
            .homepageImagesContainerLeft{
                padding-left: 0;
                padding-right: 20px;
                direction: ltr;

                .homepageTopImageItem:first-of-type{
                    text-align: start;
                }
            }
        }
    }
    .imageToVideo{
        img{
            margin-right: 0;
            margin-left: 5.5vw;
        //     right: unset;
        //     left: 120px;
        }
    }
}

// about us
.homepageAboutUS{
    background-color: var(--brighter);

    .homepageAboutUsContent{
        display: flex;
        padding: 65px 0;
        text-align: start;
        justify-content: space-between;
        align-items: center;
        position: relative;
        margin: auto;
        width: clamp(250px, 90%, 1700px);


        .homepageAboutUsIntro{
            font-family: var(--fontSecondary);
            font-size: 40px;
            font-weight: 700;
            width: 825px;
        }

        .homepageAboutUsParagraph{
            width: 57vw;
            font-family: var(--fontPrimary);
            font-size: 32px;
            font-size: clamp(22px, 2vw, 32px);
            font-weight: 400;
            text-align: start;
            margin-top: 30px;
            width: 825px;
            width: clamp(572px, 57vw, 825px);
        }

        .homepageRight{
            min-width: 315px;
            text-align: center;

            .homepageTopImageItem{
                z-index: 1;
                position: absolute;
                right: 100px;
                top: 120px;
                border-radius: 0px;

                img{
                    height: clamp(126px, 8vw, 200px);
                    width: clamp(217px, 15vw, 400px);
                    object-fit: cover;
                }
            }
        }

        .aboutUsTabletImage{
            display: none;
            width: 100%;
            text-align: center;
            width: 100%;
            position: relative;
            z-index: 1;

            .homepageTopImageItem{
                img{
                    box-shadow: 0px 21px 40px rgb(0 0 0 / 20%);
                    width: clamp(295px, 58vw, 400px);
                    height: clamp(195px, 33.6vw, 250px);
                    border-radius: 9px;
                    margin: 45px;
                    object-fit: cover;
                }
            }
        }
    }
}
// arabic for about us
.ar{
    .homepageAboutUS{
        .homepageAboutUsParagraph{
            text-align: right;
        }
    }
}

// video
.homepageLargeImage{
    // min-height: 900px;

    .videoContainer{
        width: 100%;
        visibility: hidden;

        video{
            width: 100%;
        }
    }
}

