@media only screen and (max-width: 1460px){
    .homepageBody{

        .homepageTop{
            .homepageRight{
                .homepageImagesContainerLeft{
                    padding-left: 0 !important;
                }
            }
        }

        .homepageRight{

            .homepageImagesContainerLeft{

                .homepageTopImageItem:nth-of-type(1){
                    margin-right: -50px;
                }
            }
        }
    }
    .ar{
        .homepageBody{

            .homepageRight{

                .homepageImagesContainerLeft{

                    .homepageTopImageItem:nth-of-type(1){
                        margin-left: -50px;
                        margin-right: 0 !important;
                    }
                }
            }
        }
    }
}
@media only screen and (max-width: 1200px){
    .homepageImagesContainerLeft, .homepageImagesContainerRight{
        padding-right: 0 !important;
    }
}
@media only screen and (max-width: 1050px){
    .homepageBody{

        .homepageTop{
            padding: 100px 0;

            .homepageRight{
                margin: 0;

                .homepageImagesContainerRight{
                    padding: 0;
                }
            }

            .homepageLeft{
                .mainSiteIntro{
                    // font-size: 8.7vw;
                    font-size: clamp(10px, 8.7vw, 40px);
                }
                .secondSiteIntro{
                    // font-size: 7.6vw;
                    font-size: clamp(10px, 8.7vw, 35px);
                }
                .thirdSiteIntro{
                    font-size: 20px;
                }
                .getInTouch{
                    // font-size: 4.2vw;
                    font-size: clamp(10px, 4.2vw, 20px);
                }
            }
        }
        .homepageAboutUS{
            .homepageAboutUsContent{
                .homepageLeft{
                    .homepageAboutUsIntro{
                        font-size: clamp(10px, 9vw, 30px);
                    }
                }
            }
        }
    }
}
@media only screen and (max-width: 955px){
    .homepageBody{
        .dotLottie{
            // display: none;
            top: 415px;

            .dotLottieDots{
                // left: 0 !important;
                display: none;
            }
            .dotLottieCircle{
                // top: 300px !important;
            }
            .dotLottieSplashes{
                // top: 700px !important;
                // left: 0 !important;
                display: none;
            }
        }
        .imageToVideo{
            display: none;
        }
        .homepageTop{
            padding: 30px 0;
            flex-wrap: wrap;

            .homepageLeft{
                .thirdSiteIntro{
                    width: 100%;
                }

                .thirdSiteIntro{
                    margin-top: 20px;
                }

                .getInTouch{
                    margin-top: 30px;
                }
            }

            .homepageRight{
                flex-direction: column;
                margin-bottom: clamp(65px, 17.3vw, 125px);

                .homepageTopImageItem{
                    flex-grow: 1;
                    margin: 150px 21px 0 21px !important;

                    img{
                        width: clamp(217px, 58vw, 300px);
                        height: clamp(126px, 33.6vw, 170px);
                    }
                }

                .homepageImagesContainerLeft{
                    // display: flex;
                    justify-content: space-between;
                    flex-wrap: wrap;
                    flex-direction: row-reverse;
                    display: none;

                    .homepageTopImageItem:first-of-type{
                        // width: clamp(250px, 45%, 500px);
                        margin: 0;
                        text-align: right;
                    }
                    .homepageTopImageItem:nth-of-type(2){
                        // width: clamp(250px, 70%, 500px);
                        text-align: start;
                        width: auto;

                        img{
                            margin-top: 0;
                        }
                    }

                }

                .homepageImagesContainerRight{
                    display: flex;
                    justify-content: space-between;
                    flex-wrap: wrap;
                    padding-left: 0;
                    flex-direction: row-reverse;

                    .homepageTopImageItem{
                        // width: clamp(250px, 40%, 400px);
                        margin: 100px 5px 0 5px;
                        flex-grow: 1;
                        display: none;
                    }

                    .homepageTopImageItem:first-of-type{
                        // width: clamp(250px, 45%, 500px);
                        text-align: right;
                    }

                    .homepageTopImageItem:nth-of-type(2){
                        text-align: start;
                        display: block;
                    }
                }
            }
        }

        .homepageAboutUS{
            background-color: var(--brighter);

            .homepageAboutUsContent{
                flex-wrap: wrap;
                position: relative;

                .aboutUsTabletImage{
                    display: block;
                }

                .homepageLeft{
                    width: 100%;

                    .homepageAboutUsIntro{
                        width: 100%;
                    }
                }
                .homepageRight{
                    position: absolute;
                    right: -50px;
                    bottom: 400px;
                    pointer-events: none;
                }
            }

            .homepageAboutUsParagraph{
                width: 100%;
            }
        }
    }
}
