
.inputContainer{
    margin-top: 35px;
    display: flex;
    flex-direction: column-reverse;
    height: 52px;
    min-height: 52px;
    justify-content: space-between;

    input, textarea{
        border: none;
        border-bottom: solid var(--dark) 1px;
        outline: none;
        -webkit-appearance: none;
        box-shadow: none !important;
        flex-grow: 1;
        font-family: var(--fontSecondary);
        font-size: 15px;
        font-weight: 400;
    }

    textarea {
        resize: none;
        margin: 15px 0;
    }

    input[type="tel"]{
        padding-left: 25px;
    }

    label{
        position: relative;
        width: fit-content;
        font-family: var(--fontSecondary);
        font-size: 15px;
        font-weight: 400;
        transition: font-size 0.2s ease-in-out, top 0.2s ease-in-out, left 0.2s ease-in-out;
        top: 15px;
        pointer-events: none;
    }

    label:after{
        position: absolute;
        color: var(--highlight);
        content: '*';
        right: -10px;
        top: 5px;
    }

    input:focus ~ label, input:not(:placeholder-shown) ~ label, textarea:focus ~ label, textarea:not(:placeholder-shown) ~ label{
            font-family: var(--fontSecondary);
            font-size: 11px;
            font-weight: 400;
            top: 0;
    }

    .phoneInput{
        display: flex;
        
        input{
            flex-grow: 1;
        }

        // country code select
        .css-b62m3t-container{
            border-bottom: solid var(--dark) 1px;
            width: 150px;

            *{
                border: none;
                outline: none;
                -webkit-appearance: none;
                box-shadow: none !important;
                cursor: pointer;
                transition: none;
            }

            .css-1u9des2-indicatorSeparator{
                display: none;
            }

            .css-1nmdiq5-menu > div{
                height: 150px;
                max-height: 150px;
                overflow: scroll;

            }
            .css-1nmdiq5-menu{
                box-shadow: 0px 10px 10px 0px #00000024 !important;

                *{
                    color: var(--darl);
                    background-color: var(--bright);

                    *:hover{
                        color: var(--highlight);
                    }
                }
            }

            .countryCodeSelectOption{
                display: flex;
                flex-direction: row;
                justify-content: space-between;

                .homepageTopImageItem{
                    width: 21px;
                    height: 15px;
                    // margin-right: 10px;
                    border-radius: 5px;
                    overflow: hidden;
                }

                div{
                    font-family: var(--fontSecondary);
                    font-size: 15px;
                    font-weight: 400;

                }
            }

            .css-1xc3v61-indicatorContainer{
                color: var(--dark);
            }
        }

    }

    .interestInput{

        // interest select
        .css-b62m3t-container{
            border-bottom: solid var(--dark) 1px;

            *{
                border: none;
                outline: none;
                -webkit-appearance: none;
                box-shadow: none !important;
                cursor: pointer;
                transition: none;
            }

            .css-1u9des2-indicatorSeparator{
                display: none;
            }

            .css-1nmdiq5-menu > div{
                height: 125px;
                max-height: 125px;
                overflow: scroll;

            }
            .css-1nmdiq5-menu{
                box-shadow: 0px 10px 10px 0px #00000024 !important;

                *{
                    color: var(--darl);
                    background-color: var(--bright);

                    *:hover{
                        color: var(--highlight);
                    }
                }
            }

            .countryCodeSelectOption{
                display: flex;
                flex-direction: row;

                .homepageTopImageItem{
                    width: 21px;
                    height: 15px;
                    margin-right: 10px;
                    border-radius: 5px;
                    overflow: hidden;
                }

                div{
                    font-family: var(--fontSecondary);
                    font-size: 15px;
                    font-weight: 400;

                }
            }

            .css-1xc3v61-indicatorContainer{
                color: var(--dark);
            }
        }
    }

    .phoneInput ~ label, .interestInput ~ label{
        font-family: var(--fontSecondary);
        font-size: 11px;
        font-weight: 400;
        top: 0;
    }
}