
.homepageBody{
    margin-top: 110px;
    position: relative;
}

/* links global */
.toLink{
    font-family: var(--fontPrimary);
    font-size: 24px;
    font-size: clamp(17px, 1.7vw, 24px);
    font-weight: 600;
    margin-top: 55px;
    text-transform: uppercase;
    display: flex;
    width: fit-content;
    border-bottom: solid 1px var(--dark);
    cursor: pointer;
    transition: all 0.3s ease-in-out;

    .arrowIcon{
        margin-left: 15px;
    }
}
// arabic for links global
.ar{
    .toLink{
        .arrowIcon{
            margin-left: 0;
            margin-right: 15px;
            transform: rotateY(180deg);
        }
    }
}

.toLink:hover{
    filter: brightness(0) saturate(100%) invert(19%) sepia(80%) saturate(3115%) hue-rotate(344deg) brightness(71%) contrast(94%);
    padding: 0px 10px 7px;
    margin-top: 48px;
}

    

/* Lottie Animation */
.dotLottie{
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    display: flex;
    margin: 0 auto;
    text-align: center;
    justify-content: center;
    z-index: -1;

    .dotLottieContainer{
        visibility: visible;
        width: clamp(250px, 90%, 1700px);
        margin: 0 auto;
        position: relative;

        .dotLottieCircle{
            width: 100%;
            overflow: hidden;
        }
        .dotLottieCircle{
            position: absolute;
            z-index: -1;
            width: clamp(215px, 21vw, 300px);
            height: clamp(215px, 21vw, 300px);
            right: 0;
            top: 146px;
            border-radius: 0px;
        }
        .dotLottieSplashes{
            position: absolute;
            z-index: -1;
            width: clamp(215px, 21vw, 300px);
            height: clamp(215px, 21vw, 300px);
            left: 727px;
            left: clamp(150px ,50% , 727px);
            top: 411px;
            border-radius: 0px;
        }
        .dotLottieDots{
            position: absolute;
            z-index: -1;
            height: clamp(200px, 20px, 276px);
            width: clamp(200px, 20px, 276px);
            left: clamp(150px ,53% , 768px);
            top: 6px;
            border-radius: 0px;
        }
    }
}
.dotLottieManyDots{
    position: absolute;
    height: 500px;
    width: 500px;
    right: 0;
    top: 0;
    border-radius: 0px;
    transform: rotateZ(-90deg);
    transition: all 0.2s ease-in-out;
}
// arabic for Lottie Animation
.ar{
    .dotLottie{
        direction: ltr;
    }
    .dotLottieCircle{
        right: unset !important;
        left: 0;
    }
    .dotLottieSplashes{
        left: unset !important;
        right: clamp(150px, 50%, 727px);;
    }
    .dotLottieDots{
        left: unset !important;
        right: clamp(150px, 53%, 768px);
    }
    .dotLottieManyDots{
        right: unset !important;
        left: 0;
    }
}

