// footer
.footerBody{
    background-color: var(--dark);
    color: var(--brighter);
    padding: 80px 70px;
    display: flex;
    justify-content: space-between;

    .footerTitle{
        font-family: var(--fontSecondary);
        font-size: 23px;
        font-weight: 500;
        text-align: start;
    }

    .footerNewsLetter{

        .footerTitle{
            width: 419px;
            width: clamp(350px, 29vw, 419px);
        }

        .inputContainer{
            margin: 35px 0 75px 0;
            display: flex;
            flex-direction: column-reverse;
            height: 52px;
            min-height: 52px;
            justify-content: space-between;
            width: 100%;
            display: flex;
            flex-direction: column-reverse;
            align-items: start;

            input, input:focus, input:active{
                border: none;
                border-bottom: solid var(--brighter) 1px;
                background-color: var(--dark);
                outline: none;
                -webkit-appearance: none;
                box-shadow: none !important;
                flex-grow: 1;
                font-family: var(--fontSecondary);
                font-size: 15px;
                font-weight: 400;
                color: var(--brighter);
                width: 100%;
            }

            textarea {
                resize: none;
                margin: 15px 0;
            }
    
            input[type="tel"]{
                padding-left: 25px;
            }

            label{
                position: relative;
                width: fit-content;
                font-family: var(--fontSecondary);
                font-size: 15px;
                font-weight: 400;
                transition: font-size 0.2s ease-in-out, top 0.2s ease-in-out, left 0.2s ease-in-out;
                top: 15px;
                pointer-events: none;
            }
    
            label:after{
                position: absolute;
                color: var(--highlight);
                content: '*';
                right: -10px;
                top: 5px;
            }

            input:focus ~ label, input:not(:placeholder-shown) ~ label, textarea:focus ~ label, textarea:not(:placeholder-shown) ~ label{
                    font-family: var(--fontSecondary);
                    font-size: 11px;
                    font-weight: 400;
                    top: 0;
            }

        }

        .subscribeButton{
            text-align: start;

            button{
                border: none;
                background-color: var(--highlight);
                color: var(--brighter);
                height: 48px;
                left: 70px;
                border-radius: 6px;
                padding: 0 43px;
                font-family: var(--fontSecondary);
                font-size: 16px;
                font-weight: 500;
                transition: all 0.2s ease-in-out;
            }

            button:hover{
                background-color: var(--lightHighlight);
            }
        }
    }

    .footerColumn{
        display: flex;
        flex-direction: column;
        text-align: start;

        .footerTitle{
            margin-bottom: 12px;
        }

        .footerColumnItem{
            width: fit-content;
            font-family: var(--fontPrimary);
            font-size: 16px;
            font-weight: 400;
            text-transform: uppercase;
            margin: 12px 0;
            display: flex;
            cursor: pointer;
            text-decoration: none;
            color: var(--brighter);

            text-decoration: none;
            color: var(--brighter);

            .homepageTopImageItem{
                margin-right: 10px;
                height: 20px;
                width: 20px;

                svg{
                    filter: invert(100%);
                    width: 100%;
                }
            }

            .locationHeroIcon{
                width: 20px;
                height: 20px;
                margin-right: 10px;
            }

            .locationHeroIcon.location{
                color: var(--highlight);
            }
        }

        .footerColumnItem:hover{
            filter: brightness(0) saturate(100%) invert(17%) sepia(69%) saturate(4703%) hue-rotate(348deg) brightness(68%) contrast(88%);
        }
    }
}
// arabic for footer
.ar{
    .footerBody{
    
        .footerNewsLetter{
    
            .subscribeButton{
    
                button{
                    left: unset;
                    right: 70px;
                }
            }

            .inputContainer{

                label:after{
                    left: -10px;
                    right: unset;
                }
            }
        }
    }
    .footerColumn{
        text-align: right;
        align-items: flex-start;

        .footerColumnItem{
            

            .locationHeroIcon, .homepageTopImageItem{
                margin-left: 10px;
                margin-right: 0;
            }
        }
    }

    .footerTitle{
        text-align: right;
    }
}
// media
@media only screen and (max-width: 1200px){
    .footerBody{
        flex-wrap: wrap;

        .footerColumn{
            padding: 25px;
        }
    }
}
@media only screen and (max-width: 830px){
    .footerBody{
        flex-wrap: wrap;
        padding: 50px 15px;

        .footerColumn{
            margin-top: 60px;
            padding: 0;
        }
        
        .footerColumn.parentOfTwoColumns{
            width: 100%;
        }

        .footerColumn{
            width: calc(50% - 30px);
        }

        .footerTitle{
            width: 100% !important;
        }
    }
}