.footerContainer{
    position: relative;
    overflow: hidden;

    .footerAnimation{
        position: absolute;
        bottom: 0;
        width: 100%;
        z-index: 0;
        background-color: var(--dark);

        .animatedFooter{
            transition: transform 1s ease-in-out;
            // transform: translateY(-100%);
        }
    }
}
// media
@media only screen and (max-width: 830px){
    .footerContainer{
    
        .footerAnimation{
            position: inherit;
        }
    }
}