

.cursorContainer{
    position: fixed;
    z-index: 99999;
    top: 0;
    left: 0;
    opacity: 0;
    pointer-events: none;
    width: 0;
    height: 0;
    transition: all 0.2s ease-in-out;

    .big{
        z-index: 999;
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: var(--dark);
        border-radius: 50%;
        color: var(--brighter);
        font-family: var(--fontPrimary);
        font-size: 28px;
        font-weight: 400;
        line-height: 34px;
        letter-spacing: 0em;
        text-align: center;
        overflow: hidden;
        margin-top: -50%;
        margin-right: 50%;

        
        // animation-name: stretch;
        // animation-duration: 2.0s;
        // animation-timing-function: ease-in-out;
        // animation-direction: alternate;
        // animation-iteration-count: infinite;
        // animation-play-state: running;
    }
}
  
.cursorContainer.show{
    opacity: 1;
    width: 150px;
    height: 150px;
}
  
