.toggleMobileNav{
    // hide mobile navbar on normal screens
    display: none;

    width: 42px;
    height: 42px;
    border: solid var(--dark) 1px;
    border-radius: 50%;
    margin-left: 50px;
    flex-direction: column;
    justify-content: space-evenly;
    cursor: pointer;
    background-color: var(--brighter);
    transition: background-color 0.2s ease-in-out;

    .toggleLine{
        width: 65%;
        margin: 0 auto;
        height: 10px;
        transition: transform 0.5s ease-in-out;
    }

    .toggleLine:first-of-type{
        border-bottom: solid var(--dark) 1px;
    }
    
    .toggleLine:nth-of-type(2){
        border-top: solid var(--dark) 1px;
    }
}

.toggleMobileNav:hover{
    background-color: var(--dark);

    .toggleLine{
        border-color: var(--brighter);
    }
}

.mobileNavbarBody{
    display: flex;
    position: fixed;
    width: 100%;
    height: 100%;
    background-color: var(--brighter);
    left: 0;
    top: 0;
    z-index: -1;
    padding: 100px 25px;

    
    .mobileNavbarLinks{
        display: flex;
        flex-direction: column;
        color: var(--dark);
        margin: 0;
        padding: 0;

        li{
            margin: 25px 0;
            height: 30px;
            cursor: pointer;
            position: relative;
            transition: all 0.2s ease-in-out;
            display: flex;
            align-items: center;

            a{
                text-decoration: none;
                color: var(--dark);
            }
        }

        li::after{
            content: '';
            position: absolute;
            bottom: -10px;
            left: 0;
            transition: all 0.2s ease-in-out;
        }
        
        li:hover{
            a{
                color: var(--highlight);
            }
        }

        li:hover::after{
            width: 100%;
            height: 1px;
            background-color: var(--highlight);
            bottom: 0;
            left: 0;
        }
    }
}

// arabic
.ar{
    .toggleMobileNav{
        margin-left: unset;
        margin-right: 50px;
    }
}

// mobiles and extra small screens
@media only screen and (max-width: 955px){
    .mobileNavContainer{
        display: block !important;
        order: 3;

        .toggleMobileNav{
            display: flex;
        }
    }
}