form.meetingForm{
    flex-grow: 1;
    background-color: var(--brighter);
    border-radius: 20px;
    margin: 22px;
    box-shadow: 5px 20px 40px 10px #00000012;

    .meetingPersonalInfo{
        flex-direction: column;
        text-align: start;
        padding: 50px 40px 0 50px;
    
        .meetingPersonalTitle{
            font-family: var(--fontSecondary);
            font-size: 23px;
            font-weight: 700;
            line-height: 28px;
            letter-spacing: 0em;
            text-align: start;
    
        }
    
        .meetingChosenDate{
            font-family: var(--fontSecondary);
            font-size: 19px;
            font-weight: 500;
            line-height: 23px;
            letter-spacing: 0em;
            text-align: start;
            margin: 15px 0;
            display: flex;
            flex-direction: row;
    
            .meetingEditButton{
                font-family: var(--fontPrimary);
                font-size: 19px;
                font-weight: 600;
                color: var(--highlight);
                margin: 0 16px;
                border-bottom: solid var(--highlight) 1px;
                cursor: pointer;
            }
        }
    
        .meetingPersonalForm{
            display: flex;
            flex-direction: column;
            max-height: 400px;
            overflow: scroll;
            padding-right: 30px;
            // padding-bottom: 100px;
    
            .inputContainer{
                margin-top: 35px;
                display: flex;
                flex-direction: column-reverse;
                height: 52px;
                min-height: 52px;
                justify-content: space-between;
    
                input, textarea{
                    border: none;
                    border-bottom: solid var(--dark) 1px;
                    outline: none;
                    -webkit-appearance: none;
                    box-shadow: none !important;
                    flex-grow: 1;
                    font-family: var(--fontSecondary);
                    font-size: 15px;
                    font-weight: 400;
                }

                textarea {
                    resize: none;
                    margin: 15px 0;
                }
        
                input[type="tel"]{
                    padding-left: 25px;
                }

                label{
                    position: relative;
                    width: fit-content;
                    font-family: var(--fontSecondary);
                    font-size: 15px;
                    font-weight: 400;
                    transition: font-size 0.2s ease-in-out, top 0.2s ease-in-out, left 0.2s ease-in-out;
                    top: 15px;
                    pointer-events: none;
                }
        
                label:after{
                    position: absolute;
                    color: var(--highlight);
                    content: '*';
                    right: -10px;
                    top: 5px;
                }
    
                input:focus ~ label, input:not(:placeholder-shown) ~ label, textarea:focus ~ label, textarea:not(:placeholder-shown) ~ label{
                        font-family: var(--fontSecondary);
                        font-size: 11px;
                        font-weight: 400;
                        top: 0;
                }
    
                .phoneInput{
                    display: flex;
                    
                    input{
                        flex-grow: 1;
                    }
    
                    // country code select
                    .css-b62m3t-container{
                        border-bottom: solid var(--dark) 1px;
                        width: 125px;
    
                        *{
                            border: none;
                            outline: none;
                            -webkit-appearance: none;
                            box-shadow: none !important;
                            cursor: pointer;
                            transition: none;
                        }
    
                        .css-1u9des2-indicatorSeparator{
                            display: none;
                        }
    
                        .css-1nmdiq5-menu > div{
                            height: 150px;
                            max-height: 150px;
                            overflow: scroll;

                        }
                        .css-1nmdiq5-menu{
                            box-shadow: 0px 10px 10px 0px #00000024 !important;
                            width: 100px;

                            *{
                                color: var(--darl);
                                background-color: var(--bright);
    
                                *:hover{
                                    color: var(--highlight);
                                }
                            }
                        }
    
                        .countryCodeSelectOption{
                            display: flex;
                            flex-direction: row;
                            justify-content: space-between;
    
                            .homepageTopImageItem{
                                width: 21px;
                                height: 15px;
                                // margin-right: 10px;
                                border-radius: 5px;
                                overflow: hidden;
                            }

                            div{
                                font-family: var(--fontSecondary);
                                font-size: 15px;
                                font-weight: 400;

                            }
                        }
    
                        .css-1xc3v61-indicatorContainer{
                            color: var(--dark);
                        }
                    }

                }

                .interestInput{

                    // interest select
                    .css-b62m3t-container{
                        border-bottom: solid var(--dark) 1px;
    
                        *{
                            border: none;
                            outline: none;
                            -webkit-appearance: none;
                            box-shadow: none !important;
                            cursor: pointer;
                            transition: none;
                        }
    
                        .css-1u9des2-indicatorSeparator{
                            display: none;
                        }
    
                        .css-1nmdiq5-menu > div{
                            height: 125px;
                            max-height: 125px;
                            overflow: scroll;

                        }
                        .css-1nmdiq5-menu{
                            box-shadow: 0px 10px 10px 0px #00000024 !important;

                            *{
                                color: var(--darl);
                                background-color: var(--bright);
    
                                *:hover{
                                    color: var(--highlight);
                                }
                            }
                        }
    
                        .countryCodeSelectOption{
                            display: flex;
                            flex-direction: row;
    
                            .homepageTopImageItem{
                                width: 21px;
                                height: 15px;
                                margin-right: 10px;
                                border-radius: 5px;
                                overflow: hidden;
                            }

                            div{
                                font-family: var(--fontSecondary);
                                font-size: 15px;
                                font-weight: 400;

                            }
                        }
    
                        .css-1xc3v61-indicatorContainer{
                            color: var(--dark);
                        }
                    }
                }
    
                .phoneInput ~ label, .interestInput ~ label{
                    font-family: var(--fontSecondary);
                    font-size: 11px;
                    font-weight: 400;
                    top: 0;
                }
            }

            .inputContainer:has(textarea)  {
                min-height: 100px;
                height: 100px;
            }
    
            // .inputContainer:active, .inputContainer:checked, .inputContainer:checked{
            //     label{
            //         font-family: var(--fontSecondary);
            //         font-size: 11px;
            //         font-weight: 400;
            //     }
            // }
        }
    
        .hiddenInputs{
            display: none;
        }
    }
    
    .meetingFromActions{
        background-color: var(--brighter);
        width: 100%;
        height: 65px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 10px 22px;
        border-radius: 0 0 20px 20px;
        
        .submit{
            background-color: var(--highlight);
            color: var(--brighter);
            height: 42px;
            width: 201px;
            border-radius: 6px;
            display: flex;
            justify-content: center;
            align-items: center;
            font-family: var(--fontSecondary);
            font-size: 16px;
            font-weight: 500;
            border: none;
            cursor: pointer;

        }

        .backButton{
            background-color: var(--brighter);
            color: var(--highlight);
            border: solid var(--highlight) 1px;
            border-radius: 6px;
            padding: 12px 12px 12px 8px;
            font-family: var(--fontPrimary);
            font-size: 16px;
            font-weight: 600;
            line-height: 19px;
            display: flex;
            align-items: center;
            cursor: pointer;

            .homepageTopImageItem{
                display: flex;
                align-items: center;
                margin-right: 10px;

                img{
                    pointer-events: none; 
                }
            }
            
        }
    }
}
form.meetingForm.hide{
    * &{
        display: none !important;
    }
}
// ar
.ar .meetingForm{
    .meetingPersonalInfo{
        .meetingPersonalForm{
            padding-right: unset;
            padding-left: 30px;
            .inputContainer{
                label:after{
                    right: unset;
                    left: -10px;
                }
                .phoneInput{
                    direction: ltr;
                    *{
                        direction: ltr;
                        text-align: start;
                    }
                }
            }
        }
    }
    .meetingFromActions{
        .backButton{
            padding: 12px 8px 12px 12px;
            .homepageTopImageItem{
                margin-right: 0;
                margin-left: 10px;
                svg{
                    transform: rotateZ(180deg);
                }
            }
        }
    }
}