
// contact us
.homepageContactUs{
    padding: 85px 0;
    background-color: var(--brighter);

    .contactUsTop{
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        margin: auto;
        width: clamp(250px, 90%, 1700px);   

    }

    .contactDetails{
        display: flex;
        justify-content: space-around;
        margin: 100px auto;
        width: clamp(250px, 90%, 1700px);   

        .contactItem{
            display: flex;

            .contactIcon{
                width: 24px;
                height: 24px;
                color: var(--highlight);
                margin: 0 15px;
            }
            
            .contactText{
                font-family: var(--fontPrimary);
                font-size: 19px;
                font-weight: 300;
            }
        }
    }

    .contactForm{
        display: flex;
        flex-direction: row;
        background-color: var(--brighter);
        margin: auto;
        width: clamp(250px, 90%, 1700px);
        flex-wrap: wrap;

    
        .inputContainer{
            margin-top: 35px;
            display: flex;
            flex-direction: column-reverse;
            height: 52px;
            min-height: 52px;
            justify-content: space-between;
            width: 100%;

            input, textarea{
                border: none;
                border-bottom: solid var(--dark) 1px;
                outline: none;
                -webkit-appearance: none;
                box-shadow: none !important;
                flex-grow: 1;
                font-family: var(--fontSecondary);
                font-size: 15px;
                font-weight: 400;
            }

            textarea {
                resize: none;
                margin: 15px 0;
            }
    
            input[type="tel"]{
                padding-left: 25px;
            }

            label{
                position: relative;
                width: fit-content;
                font-family: var(--fontSecondary);
                font-size: 15px;
                font-weight: 400;
                transition: font-size 0.2s ease-in-out, top 0.2s ease-in-out, left 0.2s ease-in-out;
                top: 15px;
                pointer-events: none;
            }
    
            label:after{
                position: absolute;
                color: var(--highlight);
                content: '*';
                right: -10px;
                top: 5px;
            }

            input:focus ~ label, input:not(:placeholder-shown) ~ label, textarea:focus ~ label, textarea:not(:placeholder-shown) ~ label, .phoneInput ~ label{
                    font-family: var(--fontSecondary);
                    font-size: 11px;
                    font-weight: 400;
                    top: 0;
            }

        }

        .inputContainer:nth-of-type(1), .inputContainer:nth-of-type(2), .inputContainer:nth-of-type(3), .inputContainer:nth-of-type(4){
            width: 50%;
            padding: 0 10px;
        }

        .inputContainer.textareaContainer{
            height: 150px;
            padding: 0 10px;
        }

        label{
            position: absolute;
            top: 20px;
            left: 0;
            z-index: 1;
        }
        label::after{
            content: '*';
            color: var(--highlight);
            margin-left: 5px;
        }


        label.fullNameLabel::after{
            left: 100px;
        }

        .submitContactForm{
            text-align: start;
            margin-top: 25px;

            button{
                height: 48px;
                border-radius: 6px;
                padding: 0 43px;
                border: none;
                background-color: var(--highlight);
                color: var(--bright);
                font-family: var(--fontSecondary);
                font-size: 16px;
                font-weight: 500;
                text-align: center;
                transition: all 0.2s ease-in-out;
            }

            button:hover{
                background-color: var(--lightHighlight);
            }
        }

        
    }
}
