// media
@media only screen and (max-width: 830px){
    .homepageContactUs{

        .contactDetails{
            flex-direction: column;
            margin: 48px auto;

            .contactItem{
                margin: 15px 0;

                .contactText{
                    text-align: start;
                }
            }
        }

        .contactForm{
            flex-direction: column;

            .inputContainer{
                width: 100% !important;
            }
        }
    }
}