.App {
  text-align: center;
  scroll-behavior: smooth;
}

.App > div{
  overflow: hidden;
}

// arabic direction
.App.ar{
  direction: rtl;
}
.ar{
  *{
    text-align: right;
  }
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

// fonts
@font-face {
  font-family: JannaLTBold;
  src: url(./assets/fonts/JannaLTBold.ttf);
}

/* roots */
:root {
  // colors
  --dark: #0A0A0A;
  --gray: #252525;
  --lightGray: #A3A3A3;
  --grayBackground: #E0E0E0;
  --grayFont: #666666;
  --bright: #FBFBFB;
  --brighter: #FFFFFF;
  --highlight: #A61E22;
  --lightHighlight: #d0363b;
  --lighterHighlight: #DBA5A7;
  --backgroundHighlight: #F7E9E9;
  --pureRed: #EF4444;
  // fonts
  --fontPrimary: 'Hind Madurai', 'JannaLTBold';
  --fontSecondary: 'Montserrat', 'JannaLTBold';
}

// scrollbar
::-webkit-scrollbar{
    width: 6px;
    background-color: rgpa(0,0,0,0);
}
::-webkit-scrollbar-thumb{
    border-radius: 50px;
    background-color: var(--grayBackground)
}

/* global */
*{
  box-sizing: border-box;

  -webkit-tap-highlight-color: transparent !important;
}

body {
  font-family: JannaLTBold;
  background-color: var(--bright);
  font-family: var(--fontSecondary);
}

button {
  cursor: pointer;
  font-family: var(--fontSecondary);
}

// sections
.sectionTitle{
  font-size: clamp(30px, 5.5vw, 80px);
  color: var(--brighter);
  text-shadow: -1px 0 black, 0 1px black, 1px 0 black, 0 -1px black;
  font-weight: 700;
  font-family: var(--fontSecondary);
  width: 500px;
  text-align: start;
  margin-left: 110px;
  transition: all 0.3s ease-in-out;
  position: relative;
}
.sectionTitle::before{
  position: absolute;
  content: '';
  height: 7px;
  width: 91px;
  width: clamp(65px, 6.3vw, 90px);
  left: -125px;
  left: clamp(-100px, -8.7vw, -125px);
  top: 40px;
  border-radius: 0px;
  background-color: var(--highlight);
}
.sectionTitle:hover{
  color: var(--highlight);
  text-shadow: none;
}
.ar{
  .sectionTitle{
      text-align: right;
      margin-right: 110px;
      margin-left: 0;
  }
  .sectionTitle::before{
      left: unset;
      right: clamp(-100px, -8.7vw, -125px);
      top: 65px;
  }
}
.sectionSlogan{
    font-family: var(--fontSecondary);
    font-size: clamp(24px, 2.5vw, 33px);
    font-weight: 600;
    letter-spacing: 0em;
    text-align: start;
    width: 300px;
    align-self: flex-start;
}
.ar .sectionSlogan{
    text-align: start;
}
// media
@media only screen and (max-width: 830px){

  .ourServicesTop, .ourProjectsTop, .contactUsTop{
    flex-direction: column !important;
    align-items: center !important;

      .sectionTitle{
          font-size: clamp(10px, 14vw, 50px);
          width: 60vw;
          max-width: 425px;
          margin-left: 0;
          margin-right: 0;
      }

      .sectionTitle::before{
          left: -14vw;
          width: 10vw;
          max-width: 75px;
          height: 5px;
          top: 30px;
      }

      .sectionSlogan{
          font-size: clamp(10px, 8vw, 30px);
          padding-top: 40px;
          align-self: center;
          width: 60vw;
          max-width: 425px;
          text-align: start;
      }
  }
  // ar
  .ar{
    .ourServicesTop, .ourProjectsTop, .contactUsTop{

        .sectionTitle::before{
            right: -14vw;
            top: 50px;
        }
      }
  }
}
