.creditsContainer{
    .creditsBody{
        background-color: var(--dark);
        padding: 32px 40px;
        color: #fff;
        display: flex;
        flex-direction: column;

        .creditsTop{
            display: flex;
            justify-content: space-between;
            font-family: var(--fontSecondary);
            font-size: 23px;
            font-weight: 400;
            text-align: start;



            .creditsRight{
                display: flex;
                margin-right: 60px;

                .creditsRed{
                    color: var(--highlight);
                    font-family: var(--fontSecondary);
                    font-size: 23px;
                    font-weight: 700;
                    text-align: start;
                }

                .creditsWhite{
                    font-family: var(--fontSecondary);
                    font-size: 23px;
                    font-weight: 300;
                    text-align: start;
                    display: flex;

                    .wordWithUnderlines{
                        font-weight: 700;
                        position: relative;
                    }

                    .wordWithUnderlines::before{
                        content: '';
                        background: url('../../assets/images/line.svg') no-repeat;
                        width: 50px;
                        height: 50px;
                        position: absolute;
                        left: 0;
                        top: 30px;
                    }
                    
                    .wordWithUnderlines::after{
                        content: '';
                        background: url('../../assets/images/line2.svg') no-repeat;
                        width: 50px;
                        height: 50px;
                        position: absolute;
                        left: 5px;
                        top: 40px;
                    }
                }
            }
        }
    }
    .copyrights{
        display: flex;
        background-color: var(--dark);
        color: var(--brighter);
        padding: 32px 40px;

        .copyrightsText{
            font-family: var(--fontSecondary);
            font-size: 14px;
            font-weight: 600;   
            color: #bbbbbb;      
        }
        .copyrightsFocus{
            font-family: var(--fontSecondary);
            font-size: 14px;
            font-weight: 700;
            margin: 0 25px;
        }
    }
}
.ar{
    .creditsContainer{
        direction: ltr;
    }
}
// media
@media only screen and (max-width: 830px){
    .creditsContainer{
        .creditsBody{
            
            .creditsTop{
                flex-direction: column;

                .creditsRight{
                    margin: 60px 0 0 0;
                }
            }
        }
        .copyrights{
            flex-direction: column;
            text-align: start;

            .copyrightsFocus{
                margin: 25px 0;
            }
        }
    }
}