
// our projects
.homepageOurProjects{
    padding: 65px 0;
    background-color: var(--brighter);
    overflow: hidden;
    position: relative;
    
    .ourProjectsTop{
        margin: auto;
        width: clamp(250px, 90%, 1700px);   
        display: flex;
        justify-content: space-between;
    }

    .ourProjectsContent{
        margin: auto;
        width: clamp(250px, 90%, 1700px);   
        display: flex;
        flex-direction: column;
        position: relative;

        .ourProjectsItem{
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            align-items: center;
            margin-bottom: 100px;
            z-index: 1;
            position: relative;

            .ourProjectsRight{
                .homepageTopImageItem{
                    margin-top: 35px;
                    width: clamp(100px, 41vw, 590px);
                    height: clamp(50px, 51.4vw, 740px);
                    overflow: hidden;
    
                    img{
                        width: 100%;
                        height: 100%;
                        object-fit: cover;
                        transition: transform 0.2s ease-in-out;
                    }
                }
            }

            .ourProjectsRight:hover{
                cursor: none;

                img{
                    transform: scale(110%);
                }
            }

            .ourProjectDescription{
                display: flex;
                flex-direction: column;
                width: 434px;
                width: clamp(320px, 30vw, 434px);

                .projectBrandName{
                    font-family: var(--fontSecondary);
                    font-size: 22px;
                    font-size: clamp(16px, 1.5vw, 22px);
                    font-weight: 700;
                    text-align: start;
                    color: var(--highlight);
                }

                .projectTitle{
                    font-family: var(--fontSecondary);
                    font-size: 32px;
                    font-weight: 700;
                    text-align: start;
                    margin: 18px 0;
                    font-size: clamp(23px, 1.2vw, 32px);
                }

                .projectParagraph{
                    font-family: var(--fontPrimary);
                    font-size: 20px;
                    font-size: clamp(16px, 1.4vw, 20px);
                    font-weight: 400;
                    text-align: start;
                }
            }

        }

        .ourProjectsItem:nth-of-type(odd){
            flex-direction: row-reverse;
        }

        .ourProjectsLeft{
            height: fit-content;
            width: clamp(100px, 41vw, 590px);
        }
    }


    .brandingLine{
        position: absolute;
        font-family: var(--fontSecondary);
        font-style: normal;
        font-weight: 700;
        font-size: 96px;
        line-height: 120%;
        display: flex;
        height: 100px;
        padding: 0 50px;
        transition: transform 0.1s;
        justify-content: space-evenly;
        /* or 115px */

        text-transform: uppercase;
        // width: max-content;
        width: calc(3500px + 50vw);
        /* $-gray-50 */

        color: #F0F0F0;
    
        // top: 650px;
        // top: 30px;
        // transform: translateY(-50%);

            .container {
                height: 100vh;
                display: flex;
                justify-content: center;
                align-items: center;
            }
            
            .wrapper {
                height: 20%;
                width: 60%;
                background: #555;
                position: relative;
                display: flex;
                align-items: center;
                overflow: hidden;
            }
            
            .carousel {
                background: blue;
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                display: flex;
                justify-content: center;
                align-items: center;
            }
            
            .box {
                display: flex;
                align-items: center;
                justify-content: center;
                background: green;
                height: 80%;
                width: 20%;
                margin: 0;
                padding: 0;
                position: relative;
                flex-shrink: 0;
                color: black;
                font-size: 21px;
                cursor: pointer;
            }
            
            .prev {
                position: absolute;
                top: 10%;
                left: 15%;
            }
            .next {
                position: absolute;
                top: 10%;
                right: 15%;
            }
            button {
                padding: 1rem 2rem 1rem 2rem;
                border: none;
                background: #555;
                color: white;
                text-transform: uppercase;
                font-weight: bold;
                cursor: pointer;
            }
            .toggle {
                position: absolute;
                top: 10%;
            }
            
            .show-overflow {
                overflow: visible;
            }
        .square{
            width: 1800px;
            position: relative;
            left: 0;
            padding: 0 25px;
        }
    }
}
// arabic for our projects
.ar{
    .homepageOurProjects{

        .ourProjectsTop{
            
            .ourProjectsTitle{
                text-align: right;
                margin-right: 110px;
            }
    
            .ourProjectsTitle::before{
                left: unset;
                right: -135px;
                top: 75px;
            }
        }

        .ourProjectsContent{
            .ourProjectsItem{
                .ourProjectDescription{
                    .projectBrandName{
                        text-align: right;
                    }
                    .projectTitle{
                        text-align: right;
                    }
                    .projectParagraph{
                        text-align: right;
                    }
                }
            }
        }
    }

    // .brandingLine{
    //     direction: ltr;
    //     text-align: start;
    // }
}
// our projects
@media only screen and (max-width: 1200px){
    .homepageOurProjects{

        .ourProjectsContent{

            .ourProjectsItem{

                .ourProjectsLeft{
                    padding: 0 15px;
                }
            }
        }
    }  
}
@media only screen and (max-width: 830px){
    .homepageOurProjects{

        .ourProjectsContent{

            .ourProjectsItem{
                flex-direction: column-reverse !important;

                .ourProjectsRight{

                    .homepageTopImageItem{
                        width: 100% !important;
                        height: 105% !important;
                        margin-bottom: 100px;

                        img{
                            max-width: 425px;
                        }
                    }

                }

                .ourProjectsLeft{
                    width: 100%;
                    max-width: 425px;
                    padding: 0;

                    .ourProjectDescription{
                        width: 100%;
                    }
                }
            }
        }
        
        .brandingLine{
            // display: none;
            font-size: 64px;
        }
    }
}