// insights
.homepageInsights{
    background-color: var(--brighter);

    .homepageRight{
        text-align: right;
        display: flex;
        flex-direction: column;
        width: calc(100% - 400px);
        align-items: flex-start;
    }

    .insightsTitle{
        font-size: 80px;
        color: var(--brighter);
        text-shadow: -1px 0 black, 0 1px black, 1px 0 black, 0 -1px black;
        font-weight: 700;
        font-family: var(--fontSecondary);
        width: 357px;
        text-align: start;
        margin-top: 350px;
        padding-bottom: 85px;
        transform: rotate(-90deg);
        position: sticky;
        top: 350px;
        transition: all 0.2s ease-in-out;
        margin-bottom: 350px;
    }

    .insightsTitle::before{
        position: absolute;
        content: "";
        height: 7px;
        width: 91px;
        left: -125px;
        top: 40px;
        border-radius: 0px;
        background-color: var(--highlight);
    }

    .insightsTitle:hover{
        color: var(--highlight);
        text-shadow: none;
    }

    .insightsSubTitle{
        font-family: var(--fontSecondary);
        font-size: 33px;
        font-weight: 600;
        width: 433px;
        margin-bottom: 100px;
        align-self: flex-end;
    }

    .insightsItem{
        display: flex;
        width: 100%;
        justify-content: space-around;
        text-align: start;
        margin-bottom: 65px;

        .insightsLeft{
            display: flex;
            align-items: center;
        }

        .insightsBrandName{
            font-family: var(--fontSecondary);
            font-size: 22px;
            font-weight: 700;
            color: var(--highlight)
        }
        
        .insightsInnerTitle{
            font-family: var(--fontSecondary);
            font-size: 33px;
            font-weight: 700;
        }

        .insightsDescription{
            width: 517px;
            padding: 30px;

            .insightsInnerTitle{
                margin: 20px 0;
            }

            .insightsParagraph{
                font-family: var(--fontPrimary);
                font-size: 23px;
                font-weight: 400;
            }

            .insightsDate{
                font-family: var(--fontSecondary);
                font-size: 13px;
                font-weight: 500;
                color: #4D4D4D;
                margin-top: 30px;
            }

        }

        .ourinsightssRight{
            .homepageTopImageItem{
                width: 352px;
                height: 577px;
    
                img{
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                }
            }
        }

    }
}

.homepageInsights > div{
    margin: auto;
    width: clamp(250px, 90%, 1700px);
    display: flex;
    justify-content: space-between;

    position: relative;
    padding: 100px 0;
}

// arabic for insights
.ar{
    .homepageInsights{
     
        .insightsTitle{
            text-align: right;
            transform: rotate(90deg);
        }
    
        .insightsTitle::before{
            right: -125px;
            left: unset;
        }
        .insightsSubTitle{
            text-align: start;
        }
    }
}