// navbar
.Navbar{
    background-color: var(--brighter);
    padding: 24px 70px 24px 70px;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    text-transform: uppercase;
    font-family: var(--fontPrimary);
    justify-content: space-between;

    position: fixed;
    top: 0;
    z-index: 999;
    width: 100%;
    transition: transform 0.2s ease-in-out;

    .navbarLinks{
        display: flex;
        flex-direction: row;
        list-style: none;
        font-size: 16px;
        font-weight: 600;
        letter-spacing: 0em;
        text-align: center;

        li{
            margin: 0 12px;
            height: 30px;
            cursor: pointer;
            position: relative;
            transition: all 0.2s ease-in-out;
            display: flex;
            align-items: center;

            a{
                text-decoration: none;
                color: var(--dark);
            }
        }

        li::after{
            content: '';
            position: absolute;
            bottom: -10px;
            left: 0;
            transition: all 0.2s ease-in-out;
        }
        
        li:hover{
            a{
                color: var(--highlight);
            }
        }

        li:hover::after{
            width: 100%;
            height: 1px;
            background-color: var(--highlight);
            bottom: 0;
            left: 0;
        }
    }

    .navbarOptions{
        display: flex;
        font-size: 19px;
        font-weight: 700;
        letter-spacing: 0em;
        text-align: center;
        color: var(--highlight);
        align-items: center;

        .navbarLanguage{
            cursor: pointer;
            width: 42px;
            height: 42px;
            display: flex;
            justify-content: center;
            align-items: center;
            position: relative;
        }

        .navbarLanguage::after{
            width: 100%;
            height: 100%;
            background-color: var(--dark);
            color: var(--brighter);
            opacity: 0;
            top: 0;
            left: 0;
            position: absolute;
            display: flex;
            justify-content: center;
            align-items: center;
            transition: all 0.2s ease-in-out;
        }

        .navbarLanguage.en::after{
            content: 'AR';
        }

        .navbarLanguage.ar::after{
            content: 'EN';
        }

        .navbarLanguage:hover::after{
            background-color: var(--dark);
            color: var(--brighter);
            opacity: 1 !important;
        }

        .bookAMeeting{
            width: calc(131px + 50px);
            display: flex;
            justify-content: center;
            align-items: center;
            height: 50px;
            background-color: var(--highlight);
            color: #fff;
            box-shadow: 0px 10px 30px 0px #00000024;
            border: none;
            padding: 15px 25px;
            border-radius: 6px;
            font-family: var(--fontSecondary);
            font-size: 16px;
            font-weight: 500;
            margin-left: 50px;
            cursor: pointer;
            transition: all 0.2s ease-in-out;
        }

        .bookAMeeting:hover{
            background-color: var(--gray);
        }
    }
}
// arabic for navbar
.ar .Navbar{
    .navbarOptions{
        .bookAMeeting{
            margin-left: 0;
            margin-right: 50px;
        }
    }
}

.Navbar.notTop{
    box-shadow: 0px 20px 60px 0px #00000014;
}

.Navbar.hide{
    transform: translateY(-100%);
}

/* media */
// wide screens
@media only screen and (min-width: 1460px){
    .Navbar{
        .navbarLinks{
            li{
                margin: 0 1vw;
            }
        }
    }
}
// tablets and small screens
@media only screen and (max-width: 1200px){
    .Navbar{
        .navbarOptions{
            justify-content: space-between;
            width: 100%;
        }
        .mobileNavContainer{
            display: none;
        }
    }
}
@media only screen and (max-width: 1150px){
    .Navbar{
        .navbarOptions{
            justify-content: flex-end;
            width: fit-content;

            .navbarLanguage{
                order: 2;
            }

            .bookAMeeting{
                display: none;
            }
        }
    }
}
// mobiles and extra small screens
@media only screen and (max-width: 955px){
    .Navbar{
        padding: 24px 25px;

        .navbarLinks{
            display: none;
        }
        .navbarOptions{
            .bookAMeeting{
                display: none;
            }
        }
    }  
}