

// our services
.homepageOurServices{
    padding: 65px 0;
    position: relative;
    margin: auto;
    width: clamp(250px, 90%, 1700px);

    .ourServicesTop{
        display: flex;
        justify-content: space-between;

    }

    .ourServicesContent{
        display: flex;
        justify-content: space-between;
        margin-top: 100px;
        background: url("../../assets/images/Big.svg") no-repeat;
        // background-attachment: fixed;
        background-position: center;
        background-size: 500px;

        .ourServicesRight{
            border-radius: 9px;
            display: flex;
            align-items: center;
            padding-top: 70px;

            .homepageTopImageItem{
                height: clamp(290px, 28.3vw, 408px);
                width: clamp(300px, 29.5vw, 425px);
                box-shadow: 0px 40px 80px rgba(0, 0, 0, 0.25);

                img{
                    border-radius: 9px;
                    object-fit: cover;
                    width: 100%;
                    height: 100%;
                }
            }
        }

        .ourServicesLeft{
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            width: 640px;

            .ourServicesLeftItem{
                // width: clamp(450px, 48.6vw, 700px);
                transition: all 0.3s ease-in-out;
                margin-top: 30px;
                cursor: pointer;
                width: fit-content;

                .ourServicesLeftContainer{
                    border-bottom: solid 1px #000;
                    display: flex;
                    width: fit-content;
                    height: fit-content;
                    margin-top: 40px;

                    .ourServicesIcon{
                        display: flex;
                        align-items: center;

                        svg{
                            width: 100%;
                            height: 100%;
                        }

                        img{
                            transition: all 0.2s ease-in-out;
                        }
                    }
                }
            }

            .ourServicesLeftItem:hover{
                background-color: var(--dark);
                color: var(--brighter);
                padding-left: 20px;

                .ourServicesIcon{
                    filter: invert(100%);

                    img{
                        filter: invert(100%);
                    }
                }

                .rightArrow{
                    margin: 0 10px;

                    .homepageTopImageItem{
                        background-color: var(--highlight);
                    }

                }
            }

            .ourServicesParagraphs{
                display: flex;
                flex-direction: column;

                .serviceSectionTitle{
                    width: clamp(415px, 40vw, 560px);

                    .ourServicesFirstLine{
                        display: flex;
                        font-family: var(--fontSecondary);
                        font-size: clamp(20px, 2vw, 28px);
                        font-weight: 700;
                        text-align: start;
                        align-items: center;

                        .ourServicesIcon{
                            padding: 0 10px;

                            .homepageTopImageItem{
                                display: flex;

                                svg{
                                    width: 19px;
                                    height: 19px;
                                }
                            }
                        }
                    }
                    .ourServicesSecondLine{
                        font-family: var(--fontPrimary);
                        font-size: clamp(16px, 1.4vw, 20px);
                        font-weight: 400;
                        text-align: start;
                        padding: 20px 10px 10px;
                    }
                }
            }

            .rightArrow{
                // flex-grow: 1;
                display: flex;
                justify-content: center;
                align-items: center;
                transition: all 0.2s ease-in-out;

                .homepageTopImageItem{
                    height: 36px;
                    width: 36px;
                    background-color: #000;
                    border-radius: 50%;
                    display: flex;
                    justify-content: center;
                    align-items: center;

                    img{
                        width: 10px;
                        height: 100%;
                    }

                    svg{
                        transform: rotate(45deg);
                    }
                }

            }
        }
    }
}
// arabic for our services
.ar{
    .homepageOurServices{

        .ourServicesTop{

            // .sectionTitle::before{
            //     left: unset;
            //     right: 0;
            //     top: 75px;
            // }
        }
    }
    .ourServicesContent{
        .ourServicesLeft{
            .rightArrow{
                .homepageTopImageItem{
                    transform: rotateZ(180deg);
                }
            }
        }
        .ourServicesLeftItem:hover{
            margin-left: unset;
            padding-right: 20px;
        }
    }
}
// media
@media only screen and (max-width: 830px){
    .homepageOurServices{

            .ourServicesContent{
                margin-top: 15px;
                flex-direction: column;

                .ourServicesLeft{
                    width: auto;

                    .ourServicesLeftItem{
                        width: 100%;
                        border-bottom: solid 1px #000;

                        .ourServicesLeftContainer{
                            justify-content: space-evenly;
                            border-bottom: none;

                            .ourServicesParagraphs{
                                width: 85%;

                                .serviceSectionTitle{
                                    width: 100%;

                                    .ourServicesText{
                                        font-size: clamp(10px, 6.4vw, 25px);
                                    }

                                    .ourServicesSecondLine{
                                        font-size: 16px !important;
                                    }
                                }
                            }
                            .rightArrow {
                                align-items: flex-start;
                            }
                        }
                    }

                .ourServicesLeftItem:hover{
                    background-color: var(--dark);
                    color: var(--brighter);
                    padding-left: 0 !important;
                    padding-right: 0 !important;
                }

                }

            .ourServicesRight{
                display: none;
            }
        }
    }
}
